function EmployeeList() {
  //ffc shortcut
  const navigate = useNavigate();
  const view = (emp) => {
    alert("view");
    localStorage.setItem("Emp", JSON.stringify(emp));
    navigate("/employeeDetail");
  };
  return (
    <div>
      <p>Employee List</p>
      {employeeArray.map((e) => {
        // return <CardClass emp={e}></CardClass>
        return (
          <CardFunction
            emp={e}
            btnName="View More"
            ontrigger={() => view(e)}
          ></CardFunction>
        );
      })}
      {/* <CardClass></CardClass> */}
      {/* <CardFunction></CardFunction> */}
    </div>
  );
}

export default EmployeeList;
