import React from "react";
import "../css/login.css";
import { useForm } from "react-hook-form";

function Login() {
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm(); //return object

  const submit = (data) => {
    // data.preventDefault();
    console.log(data);
    fetch("https://jsonplaceholder.typicode.com/todos/", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
      body: JSON.stringify(data),
    })
      .then((res) => res.json())
      .then((data) => {
        console.log(data);
      });
  };

  return (
    <form onSubmit={handleSubmit(submit)}>
      <div id="LoginPage">
        <div class="group">
          <h1>Login</h1>
          <i class="fa-regular fa-user"></i>{" "}
          <input
            type="text"
            placeholder="Username"
            {...register("username", {
              required: {
                value: true,
                message: "username is required",
              },
              minLength: {
                value: 3,
                message: "username should have atleast 3 characters",
              },
            })}
          />
          {errors.username && (
            <small style={{ color: "red" }}>{errors.username.message}</small>
          )}
          {/* <small style={{ color: "red" }}>dummy</small> */}
          <i class="fa-solid fa-lock"></i>{" "}
          <input
            type="password"
            placeholder="Password"
            {...register("password", {
              required: {
                value: true,
                message: "password is required",
              },
              // minLength: {
              //   value: 8,
              //   message: "password should have atleast 8 characters",
              // },
              pattern: {
                value: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
                message:
                  "Minimum eight characters, at least one letter and one number:",
              },
            })}
          />
          {errors.password && (
            <small style={{ color: "red" }}>{errors.password.message}</small>
          )}
          {/* <small style={{ color: "red" }}>dummy</small> */}
          <label for="password-verify"></label>
          <a href="#">forgot password?</a>
          <div class="terms">
            <button type="submit">Login</button>
            <div class="member">
              Not a member? <a href="./signup.html">Register Now</a>
            </div>
          </div>
        </div>
      </div>
    </form>
  );
}

export default Login;
