import React from "react";
import '../css/dummy.css';
import Swal from 'sweetalert2'

function Dummy() {
    let mystyle = {
        backgroundColor: 'green',
        color: 'white'
    }

    mystyle.width = '300px';

    const [mystyle2, setmystyle2] = React.useState({
        backgroundColor: 'green',
        color: 'white'
    })

    const myAlert = () => {
        // alert('hi'); 
        Swal.fire("Hi");
        console.log('hi');
        // mystyle.height = '40px';
        // mystyle2.height = '40px';
        setmystyle2({...mystyle2,height:'40px'})
    };
    return (
        <div id="DummyPage" onClick={myAlert}>
            <div class="group">
                <p style={mystyle}>This Page for Dummy 1</p>
                <p style={mystyle2}>This Page for Dummy 2</p>
            </div>
        </div>

    );
}

export default Dummy;