import React from "react";
import "../css/pagination.css";
import Table from "../components/table/table";
function Pagination() {
  const [post, setPost] = React.useState([]);
  React.useEffect(() => {
    fetch('https://jsonplaceholder.typicode.com/todos/')
    .then(res => res.json())
    .then(data => {
      console.log(data);
      setPost(data);
    })
  }, []);

  const data = [
    { Name: "Sharan", Age: "24" },
    { Name: "Sharan 1", Age: "24" },
    { Name: "Sharan 2", Age: "24" },
    { Name: "Sharan 3", Age: "24" },
    { Name: "Byju 4", Age: "24" },
  ];
  return (
    <div id="PaginationPage">
      {/* <Table data={data}></Table> */}
      {post.length > 0 ? <Table data={post}></Table> : <p>loading...</p>}
      <div></div>
    </div>
  );
}

export default Pagination;
