import React from "react";

function Table(props) {
  // const data=props.data;

  const { data } = props;

  const [pageLimit, setPageLimit] = React.useState(10);
  const [page, setPage] = React.useState(1);
  const [lastpage, setLastPage] = React.useState(1);

  React.useEffect(() => {
    setLastPage(Math.ceil(data.length / pageLimit));
    setPage(1);
  }, [pageLimit]);

  const generateHead = () => {
    const keys = Object.keys(data[0]);
    //console.log(keys);
    return keys.map((e, i) => <th key={i}>{e}</th>);
    //   return (
    //     <th>Name</th>
    //     <th>Age</th>
    //   )
  };

  const generateData = (obj) => {
    const keys = Object.keys(obj);
    //console.log(keys);
    return keys.map((e, i) => <td key={i}>{String(obj[e])}</td>);
  };

  const generateBody = (data) => {
    return data.map((e, i) => <tr>{generateData(e)}</tr>);
  };

  const displayPage = () => {
    const start = (page - 1) * pageLimit;
    const end = start + pageLimit; // dont hard code
    const filterData = data.slice(start, end);
    return generateBody(filterData);
  };

  const generateButtons = () => {
    const buttons = [];
    const start = page - 2 < 1 ? 1 : page - 2; // or page
    const end = page + 2 > lastpage ? lastpage : page + 2;
    for (let i = start; i <= end; i++) {
      buttons.push(i);
    }
    return buttons.map((e, i) => (
      <button
        className={page === e ? "currentPage" : ""}
        key={i}
        onClick={() => setPage(e)}
      >
        {e}
      </button>
    ));
    // return (
    //   <>
    //   <button onClick={() => setPage(1)}>1</button>
    //     <button onClick={() => setPage(2)}>2</button>
    //     <button onClick={() => setPage(3)}>3</button>
    //     <button onClick={() => setPage(4)}>4</button>
    //     <button onClick={() => setPage(5)}>5</button>
    //   </>
    // )
  };

  return (
    <div>
      <div>
        <button onClick={() => setPage(1)}>First</button>
        <button onClick={() => setPage(page - 1)} disabled={page === 1}>
          Previous
        </button>
        {generateButtons()}
        <button onClick={() => setPage(page + 1)} disabled={page === lastpage}>
          Next
        </button>
        <button onClick={() => setPage(lastpage)}>Last</button>

        <select
          vale={pageLimit}
          onChange={(ev) => {
            console.log(ev);
            console.log(ev.target);
            console.log(ev.target.value);
            setPageLimit(parseInt(ev.target.value));
          }}
        >
          <option value="5">5</option>
          <option value="10">10</option>
          <option value="15">15</option>
          <option value="20">20</option>
        </select>
      </div>
      <table>
        <thead>
          <tr>{generateHead()}</tr>
        </thead>
        <tbody>
          {/* <tr>{generateData(data(0))}</tr>
          <tr>{generateData(data(1))}</tr>
          <tr>{generateData(data(2))}</tr>
          <tr>{generateData(data(3))}</tr> */}
          {displayPage()}
        </tbody>
      </table>
    </div>
  );
}

export default Table;
