import React from "react";
import CardFunction from "../components/CardFunction";
import { useNavigate } from "react-router-dom";

function EmployeeDetail() {
  let employee = {
    Name: "Aurora",
    Age: 53,
    Email: "ante.blandit@disparturient.ca",
    "Date of joining": "10/06/2019",
    "Phone number": "1628101902299",
    Street: "493 Iaculis Rd.",
    City: "Lewiston",
    Zip: "42591-180",
    Region: "Maine",
    Country: "Sudan",
    Info: "est. Nunc ullamcorper, velit in aliquet lobortis, nisi nibh lacinia",
    EmployeeID: 101,
  };
  if (localStorage.getItem("Emp")) {
    employee = JSON.parse(localStorage.getItem("Emp"));
  }
  const navigate = useNavigate();

  console.log(Object.keys(employee));

  const Back = () => {
    // alert("Back");
    navigate("/employeeList");
  };
  return (
    <div>
      <p>EmployeeDetail</p>
      <CardFunction
        emp={employee}
        btnName="Back"
        ontrigger={() => Back()}
      ></CardFunction>
      <table>
        <tbody>
          {Object.keys(employee).map((e) => {
            return (
              <tr>
                <th>{e}</th>
                <td>{employee[e]}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );
}

export default EmployeeDetail;
