import React from "react";
import profile from "../images/download.png";
import "../css/Card.css";

function CardFunction(props) {
  return (
    <div className="Empcard">
      <img src={profile} alt="profile img" />
      <p>Card</p>
      <p>{props.emp.Name}</p>
      <p>{props.emp.EmployeeID}</p>
      <button onClick={props.ontrigger}>{props.btnName}</button>
    </div>
  );
}

export default CardFunction;
